import {Injectable} from '@angular/core';
import {MyClappService, SponsorModel, UpdateSponsorRequestParams} from '@myclapp/myclapp-server';
import {Observable, of} from 'rxjs';
import {map} from 'rxjs/operators';

import {BaseConfigService} from './base.config.service';
import {UserService} from './user.service';

@Injectable({providedIn: 'root'})
export class SponsoringService extends BaseConfigService<SponsorModel> {

    constructor(private apiClient: MyClappService, private userService: UserService) {
        super(SponsoringService.name);
    }

    _cachedConfig: SponsorModel[] | null = null;

    _getCachedConfig(): SponsorModel[] | null {
        return this._cachedConfig;
    }

    _refresh(): Observable<SponsorModel[]> {
        this._logger.debug('Refreshing sponsor list')
        return this.apiClient
            .getSponsors({clubId: this.userService.club_id})
            .pipe(map(res => {
                this._logger.debug('Received SponsorListModel', res)
                const returnList = []
                for (const item of res.items) {
                    returnList.push(item)
                }
                this._logger.debug('Inferred sponsor list', returnList)
                this._cachedConfig = returnList;
                return returnList
            }))
    }

    get(refresh?: true): Observable<SponsorModel[]> {
        if (this._cachedConfig == null || refresh) return this._refresh()
        // @ts-ignore null check above
        return of(this._getCachedConfig())
    }

    submit(data: SponsorModel): Observable<SponsorModel> {
        if (data.id === -1) return this._createSponsor(data)
        return this.apiClient.updateSponsor({
            clubId: this.userService.club_id,
            body: data, sponsorId: data.id
        } as UpdateSponsorRequestParams)
    }

    private _createSponsor(data: SponsorModel): Observable<SponsorModel> {
        return this.apiClient.createSponsor({clubId: this.userService.club_id, body: data})
    }


}
