import {ChangeDetectionStrategy, Component} from '@angular/core';
import {
    FormBuilder, FormGroup,
} from '@angular/forms';
import {SponsorModel} from '@myclapp/myclapp-server';
import {BehaviorSubject} from 'rxjs';

import {BaseConfigComponent} from '../../../../../helpers/base-config.component';
import {SponsoringService} from '../../../../../services/sponsoring.service';


@Component({
    selector: 'sb-clubdata',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './sponsoring.component.html',
    styleUrls: ['./sponsoring.component.scss'],
})
export class SponsoringComponent extends BaseConfigComponent<SponsorModel[]> {
    _form: FormGroup;
    _mappedModel$: BehaviorSubject<SponsorModel[]> = new BehaviorSubject<SponsorModel[]>([]);
    formLayout: any;

    constructor(private fb: FormBuilder, private sponsoringService: SponsoringService) {
        super(SponsoringComponent.name);
        this._logger.debug('Constructing component')
        this._form = this.fb.group({
            secondary_color_hex: [''],
            primary_color_hex: [''],
            name: [''],
            id: [],
            category: ['']
        });

        this._initialStatus$.next({status: 'pending'})
        this.sponsoringService.get().subscribe(data => {
            this._initialStatus$.next({status: 'success'});
            this._mappedModel$.next(data);
            setTimeout(() => {
                this._initialStatus$.next({status: 'idle'})
            }, 300);
        });
        this._mappedModel$.subscribe(data => {
            this._logger.debug('_mappedModel updated', data)
            // TODO FILL _Form patch values
        });
    }

    protected _submitData(): void {
        const data: SponsorModel = {
            ...this._form.value
        }
        this.sponsoringService.submit(data).subscribe(res => {
            this._logger.debug('Received submit result', res)
            this._submitStatus$.next({status: 'success'})
            setTimeout(() => {
                this._submitStatus$.next({status: 'idle'})
            }, 300)
            const copySponsors = this._mappedModel$.value;
            for (const sponsor of copySponsors) {
                if (res.id === sponsor.id) {
                    this._logger.debug('Affected sponsor before assign', sponsor)
                    Object.assign(sponsor, res)
                    this._logger.debug('Affected sponsor after assign', sponsor)
                }
            }
            this._logger.debug('Assigned received result', copySponsors)
            this._mappedModel$.next(copySponsors)
        })
    }


}
